.my-learn-details-show {
  width: 100%;
  display: block;
  background: #1E1D23;
  min-width: 1366px;
  min-height: 100vh;
  overflow-y: hidden;
}
.my-learn-details-show .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.my-learn-details-show .details-show-pic {
  display: block;
  width: 100%;
  padding: 60px 0;
}
.my-learn-details-show .details-show-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.my-learn-details-show .details-show-pic .top {
  height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  padding: 20px 60px;
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  z-index: 999;
}
.my-learn-details-show .details-show-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.my-learn-details-show .details-show-pic .top .img img {
  display: block;
}
.my-learn-details-show .details-show-pic .preview-box {
  margin: 40px 40px 0;
  display: block;
  padding-right: 28%;
  position: relative;
}
.my-learn-details-show .details-show-pic .preview-box .left {
  width: 100%;
  float: left;
  background: #111111;
  border-radius: 6.14698px;
  padding: 30px 20px;
  text-align: center;
  min-height: 300px;
  height: 100%;
}
.my-learn-details-show .details-show-pic .preview-box .left .pic_img {
  display: block;
  width: 100%;
  overflow: auto;
  min-height: 560px;
}
.my-learn-details-show .details-show-pic .preview-box .left embed {
  width: 100%;
  height: 100%;
}
.my-learn-details-show .details-show-pic .preview-box .left .preview_img {
  max-width: 100%;
  display: inline-block;
}
.my-learn-details-show .details-show-pic .preview-box .left .audio-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.my-learn-details-show .details-show-pic .preview-box .right {
  position: absolute;
  right: 0;
  top: 0;
  width: 28%;
  float: right;
  background: #111111;
  border-radius: 6.14698px;
  padding: 25px 0;
  height: 100%;
}
.my-learn-details-show .details-show-pic .preview-box .right .span {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #FFFFFF;
  padding: 0 40px 14px;
  display: block;
  border-bottom: 1px solid #343434;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic {
  min-height: auto;
  height: auto;
  overflow: inherit;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic .Catalogue-pic-tree .ant-tree-treenode-switcher-open .ant-tree-switcher {
  display: inline-block !important;
  color: #fff !important;
  margin-top: 4px;
  width: 16px;
  height: 16px;
  line-height: 16px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic .Catalogue-pic-tree .ant-tree.ant-tree-directory > li.ant-tree-treenode-selected > span.ant-tree-switcher {
  display: inline-block !important;
  color: #fff !important;
  margin-top: 4px;
  width: 16px;
  height: 16px;
  line-height: 16px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree {
  margin-top: 20px;
  padding: 0 30px;
  height: 510px;
  width: 100%;
  display: block;
  overflow-y: auto;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree .ant-tree-title .TreeIcon {
  height: 30px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree .ant-tree-title .TreeIcon div {
  background: none;
  margin-top: 10px;
  left: 10px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree .ant-tree-title .TreeIcon div img {
