}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree .ant-tree-title .TreeIcon .title1 {
  color: #FFFFFF;
  display: inline-block;
  padding: 0 100px 0 30px!important;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
