}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree .title {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 28px;
  padding-right: 26%;
  padding-left: 10px;
  width: 100%;
  position: relative;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #fff;
  margin: 4px 0;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 5px;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #535353;
}
.my-learn-details-show .details-show-pic .preview-box .right .Catalogue-pic-tree::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #ededed;
}
.my-learn-details-show .details-show-pic .preview-box .right .Tree-pic {
  width: 100%;
  display: block;
  margin-top: 20px;
}
.my-learn-details-show .details-show-pic .preview-box .right .Tree-pic .ant-tree ant-tree-directory .ant-tree-treenode-switcher-open .ant-tree-title {
